@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Playfair+Display:ital,wght@0,400;0,500;0,600;0,700;0,800;0,900;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
* {
  box-sizing: border-box
}

html,
body {
  margin: 0;
  padding: 0;
  overflow: hidden;
  max-width: 100vw;
  max-height: 100vh;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: "Poppins", sans-serif;
}

.App {
  width: 70%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 250px;
  right: 0;
  bottom: 0;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  text-align: center;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}

h1 {
  /* font-size: clamp( 16px, 10vh, 250px); */
  font-weight: 700;
  line-height: 100%;
  color: #1f2a44;
  text-align: center;
  width: 100%;
  padding-bottom: 70px;
  /* padding: 0 5rem; */
  z-index: 1;
}

h2 {
  /* font-size: clamp( 16px, 10vh, 250px); */
  font-weight: 700;
  line-height: 100%;
  color: #1f2a44;
  text-align: center;
  width: 100%;
  padding-bottom: 70px;
  /* padding: 0 5rem; */
  z-index: 1;
}
.shape-container {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  width: 100vw;
  height: 100vh;
}

.svg-shapes {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  width: 100vw;
  height: 100%;
  margin: 0;
}

.svg-shapes circle {
  fill: transparent;
  stroke: url(#bggrad);
  stroke-linecap: round;
  -webkit-animation: bgmove 200s linear infinite;
          animation: bgmove 200s linear infinite;
}

@-webkit-keyframes bgmove {
  0% {
    stroke-dashoffset: 1000;
  }

  100% {
    stroke-dashoffset: 0;
  }
}

@keyframes bgmove {
  0% {
    stroke-dashoffset: 1000;
  }

  100% {
    stroke-dashoffset: 0;
  }
}

.logos {
  display: grid;
  grid-gap: 1rem;
  grid-template-columns: 1fr 0.25fr 1fr;
  place-items: center;
  background-color: #1f2a44;
  padding: clamp( 0.5rem, 5vw, 2rem);
  border-radius: 3rem;
  z-index: 2;
  box-shadow: 0 5px 10px rgba(0, 0, 0, 0.15);
}

.logos svg {
  display: block;
  width: clamp( 1rem, 5vw, 3rem);
  height: clamp( 1rem, 5vw, 3rem);
}

.seperator {
  place-self: flex-end;
  border-left: 2px solid white;
  height: 100%;
}

